@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f9fbfd;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(1, 28, 64, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  z-index: 10;
}

.header__container {
  margin: 0 auto;
  max-width: 1260px;
  display: flex;
  justify-content: space-between;
  padding: 10px 33px 10px 20px;
}
@media (max-width: 1023px) {
  .header__container {
    padding: 10px 29px 10px 28px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding: 0;
  }
}

.header__logo {
  fill: #f9fbfd;
  width: 70px;
  height: 38px;
}

.header__icon {
  margin-top: 25px;
  margin-left: 30px;
}
@media (max-width: 1023px) {
  .header__icon {
    width: 38px;
    height: 20px;
    margin-top: 20px;
    margin-left: 17px;
  }
}
@media (max-width: 767px) {
  .header__icon {
    width: 42px;
    height: 22px;
    margin-top: 20px;
    margin-left: 15px;
  }
}

.header__navigation {
  max-width: 670px;
}
@media (max-width: 767px) {
  .header__navigation {
    display: flex;
    flex-direction: column;
  }
}

.header__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .header__list {
    margin: 1px 5px 0 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin-top: 4px;
  }
}

.header__item {
  padding: 15px 17px;
}
@media (max-width: 1023px) {
  .header__item {
    padding: 15px 12px;
  }
}
@media (max-width: 767px) {
  .header__item {
    padding: 13px 22px;
  }
}

.header__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  cursor: pointer;
  user-select: none;
}
.header__link:hover {
  transition: all 0.3s ease-in-out;
  color: #d8d8d9;
  text-shadow: #f9fbfd 2px 0 10px;
}
.header__link:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}

.header__toggle {
  display: none;
  padding: 0;
  width: 24px;
  height: 16px;
  background-image: url("../img/svg/burger.svg");
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
  cursor: pointer;
  border: none;
  margin-top: 23px;
  margin-right: 15px;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
    align-self: flex-end;
  }
}

@media (max-width: 767px) {
  .header--nojs .header__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .header--closed .header__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .header--opened .header__container {
    padding: 0;
  }
  .header--opened .header__navigation {
    position: relative;
    background-color: #f9fbfd;
    z-index: 11;
    width: 285px;
    height: 100vh;
    margin-left: auto;
    overflow-y: auto;
  }
  .header--opened .header__list {
    display: grid;
    grid-template-columns: 277px;
    flex-direction: column;
    position: absolute;
    top: 67px;
    right: 0;
    overflow: scroll;
    height: auto;
    max-height: 490px;
  }
  .header--opened .header__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .header--opened .header__toggle {
    background-image: url("../img/svg/close.svg");
    margin-right: 10px;
    z-index: 12;
  }
  .header--opened .header__logo {
    position: absolute;
    z-index: 12;
    top: 20px;
    right: 200px;
  }
  .header--opened .header__icon {
    fill: #011c40;
    margin: 0;
  }
}

@media (max-width: 767px) {
  .header--nojs .header__navigation {
    margin-bottom: 50px;
  }
  .header--nojs .header__list {
    display: flex;
    flex-direction: column;
  }
  .header--nojs .header__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .header--nojs .header__icon {
    fill: #011c40;
  }
}

.promo {
  width: 100%;
  margin-bottom: 120px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  padding: 170px 0 277px;
  position: absolute;
  top: 0;
  background-image: image-set("../img/background/background-desktop@2x.webp" 2x, "../img/background/background-desktop.webp" 1x);
  background-color: #3375c5;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
  min-height: 650px;
  height: auto;
}
@media (max-width: 1023px) {
  .promo {
    min-height: 638px;
    height: auto;
    padding: 66px 0;
    background-image: image-set("../img/background/background-tablet@2x.webp" 2x, "../img/background/background-tablet.webp" 1x);
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo {
    left: 0;
    height: auto;
    min-height: 500px;
    margin-bottom: 66px;
    padding: 0;
    background-image: image-set("../img/background/background-mobile@2x.webp" 2x, "../img/background/background-mobile.webp" 1x);
  }
}

.promo__text {
  padding-left: 130px;
  padding-top: 98px;
}
@media (max-width: 1023px) {
  .promo__text {
    padding-top: 200px;
    padding-left: 30px;
  }
}
@media (max-width: 767px) {
  .promo__text {
    padding-top: 174px;
    padding-left: 0;
  }
}

.promo__text h1 {
  margin: 0;
  margin-bottom: 12px;
  font-size: 54px;
  line-height: 66px;
  font-weight: 700;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo__text h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .promo__text h1 {
    font-size: 36px;
    line-height: 45px;
  }
}

.promo__text p {
  margin: 0;
  color: #f9fbfd;
  font-size: 20px;
  line-height: 24px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo__text p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .promo__text p {
    padding-right: 60px;
    font-size: 16px;
  }
}

.promo--nojs {
  position: static;
  margin-top: -105px;
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .promo--nojs {
    margin: 0;
  }
}

.page--lock {
  overflow: hidden;
}
@media (min-width: 768px) {
  .page--lock {
    overflow: visible;
  }
}

.page--lock::before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}
@media (min-width: 768px) {
  .page--lock::before {
    display: none;
  }
}

.journey {
  background-color: #f9fbfd;
}

.journey__container {
  display: grid;
  grid-template-columns: auto 48%;
  padding: 14px 50px 147px;
  gap: 80px;
}
@media (max-width: 1023px) {
  .journey__container {
    grid-template-columns: auto 48%;
    gap: 23px;
    padding: 25px 50px 77px 46px;
  }
}
@media (max-width: 767px) {
  .journey__container {
    grid-template-columns: auto;
    gap: 30px;
    padding: 25px 15px 85px 15px;
  }
}

.journey__text {
  padding-top: 30px;
}
@media (max-width: 1023px) {
  .journey__text {
    padding: 0;
    padding-right: 2px;
  }
}

.journey h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .journey h2 {
    margin-top: -10px;
    margin-bottom: 10px;
    font-size: 26px;
    line-height: 140%;
  }
}
@media (max-width: 767px) {
  .journey h2 {
    font-size: 24px;
  }
}

.journey p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}

.advantages {
  background-image: image-set("../img/background/background-advantages@2x.webp" 2x, "../img/background/background-advantages.png" 1x);
  background-color: #cfe4fb;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 562px;
  padding-top: 117px;
  padding-bottom: 40px;
}
@media (max-width: 1023px) {
  .advantages {
    min-height: 588px;
    padding-top: 95px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 61px;
    padding-bottom: 65px;
  }
}

.advantages__container {
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .advantages__container {
